import React, { useContext } from 'react'
import BlogContext from '../context/Blogs/BlogContext'
import BlogItem from './BlogItem'
function Blogs() {
    const { isChecked, blogsToDisplay, userName, } = useContext(BlogContext)
    let authToken = localStorage.getItem("authToken")

    return (
        <div className='m1-rem'>

            <h1 className='text-center silver  m1-top-rem f-w-8'><span className='spText2'>Unleash</span> Your <span className='spText1'>Techie</span> Side - {authToken !== null && isChecked ? <span>{userName}'s </span> : null}<span className='spText2'>Blogs</span>  </h1>
            <div className='container mt-4'>
                <div className='row transition '>{
                    blogsToDisplay.length === 0 ? "No Blogs To Display..." : blogsToDisplay.map((Blog) => {
                        return <div key={Blog._id} className='col-5 m1-top-rem'><BlogItem title={Blog.title} description={Blog.description} id={Blog._id} image={Blog.image} user={Blog.user} author={Blog.author} />
                        </div>

                    })}
                </div>
            </div>
        </div>
    )
}

export default Blogs