import React, { useState, useContext } from 'react'
import { Form, Button, Col, Row } from 'react-bootstrap';
import BlogContext from '../context/Blogs/BlogContext';
import { useNavigate } from 'react-router-dom';
import RichTextEd from './RichTextEd';
function AddBlog() {
    let navigate = useNavigate()


    const { addBlog, fetchBlogs, setShowAlert, setShowLoader, fetchAllBlogs, editorData, isChecked } = useContext(BlogContext)

    const [title, setTitle] = useState("")
    const [image, setImage] = useState("")
    const handleSubmit = async (e) => {
        e.preventDefault();
        setShowLoader(true);

        const BlogData = {
            title: title,
            description: editorData,
            image: image
        }
        await addBlog(BlogData);
        if (isChecked === true) {
            await fetchBlogs();
        }
        else {
            await fetchAllBlogs()
        }


        setShowLoader(false)
        await setShowAlert({
            show: true,
            msg: "Blog has Been Added Successfully!",
            condition: "success"
        })
        setTimeout(() => {
            setShowAlert({
                show: false
            })
            navigate("/Blogs")

        }, 1000)

    }
    const onTitleChange = (e) => {
        setTitle(e.target.value);
    }
    const onImageChange = (e) => {
        setImage(e.target.value);
    }
    return (
        <div className="container   vh-100 w-100">
            <Form onSubmit={handleSubmit} >
                <h1 className='mb-4 '>Add Blog</h1>

                <Form.Group className="mb-3" controlId="formBasicText" >
                    <Form.Label>Title</Form.Label>
                    <Form.Control
                        size="lg"
                        type="text"
                        placeholder="Enter title..."
                        className="rounded-pill"
                        value={title}
                        onChange={onTitleChange}
                        name='title'
                        required
                    />

                </Form.Group>
                <Form.Group className='mb-3' controlId='formBasicImg'>
                    <Form.Label>Main/Top Image</Form.Label>
                    <Form.Control type='Link' placeholder='Paste Your Image Link Here...' onChange={onImageChange} required />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicText" >
                    <Form.Label>Blog</Form.Label>
                    <Row>
                        <RichTextEd es={"empty"} />
                    </Row>

                </Form.Group>


                <Row>
                    <Col sm={2}>
                        <Button variant="primary" type="submit" className="rounded-pill w-100">
                            Submit
                        </Button>
                    </Col>
                </Row>
            </Form>
        </div>
    )
}

export default AddBlog