import React, { useContext } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import { Form, Dropdown, DropdownButton } from 'react-bootstrap';

import Navbar from 'react-bootstrap/Navbar';
import { LinkContainer } from "react-router-bootstrap"
import { useNavigate } from 'react-router-dom';
import BlogContext from '../context/Blogs/BlogContext';
function MyNavbar() {
    const { setToken, isChecked, setIsChecked, fetchAllBlogs } = useContext(BlogContext);
    let navigate = useNavigate()

    const handleSwitch = async () => {
        await setIsChecked(!isChecked)
    }
    const handelDropDown = (eventKey) => {
        if (eventKey === "Login") {
            navigate("/Login")
        }
        else if (eventKey === "SignUp") {
            navigate("/SignUp")
        }
    }
    let authToken = localStorage.getItem("authToken")
    const logout = async () => {
        localStorage.removeItem("authToken");
        setToken(localStorage.getItem("authToken"))
        await fetchAllBlogs()
        navigate("/Login")
        // window.location.reload()

    }
    return (
        <Navbar expand="lg" className="bg-light sticky-top">
            <Container>
                <LinkContainer to="/">

                    <Navbar.Brand className="text-dark">BlogHub</Navbar.Brand>
                </LinkContainer>
                <Navbar.Toggle aria-controls="basic-navbar-nav" className="navbar-toggler-white" />
                <Navbar.Collapse id="basic-navbar-nav">

                    {authToken === null ?
                        <><Nav className="me-auto">
                            <LinkContainer to="/">
                                <Nav.Link className="text-dark">Home</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to="/Blogs">
                                <Nav.Link className="text-dark">Blogs</Nav.Link>
                            </LinkContainer>


                        </Nav>
                            <DropdownButton title="Create Blogs" variant='dark' className="mx-3" onSelect={handelDropDown}>
                                <Dropdown.Item eventKey="SignUp" className='btnCol bgBtnCol' >SignUp</Dropdown.Item>
                                <Dropdown.Item eventKey="Login" className='btnCol bgBtnCol' >Login</Dropdown.Item>

                            </DropdownButton>
                        </>
                        :

                        <>
                            <Nav className="me-auto">
                                <LinkContainer to="/AddBlog">
                                    <Nav.Link className="text-dark">AddBlog</Nav.Link>
                                </LinkContainer>
                                <LinkContainer to="/Blogs">
                                    <Nav.Link className="text-dark">Blogs</Nav.Link>
                                </LinkContainer>
                                <LinkContainer to="/Login">
                                    <Nav.Link className="text-dark" onClick={logout}>Logout</Nav.Link>
                                </LinkContainer>

                            </Nav>
                            <Form className='h-100 d-flex align-items-center justify-content-center'>
                                <Form.Check type='switch' id='switch filter' label="Filter Blogs" checked={isChecked} onChange={handleSwitch} />
                            </Form>
                        </>
                    }

                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default MyNavbar;
