import Alert from 'react-bootstrap/Alert';
function MyAlert(props) {
    const { show, msg, condition } = props
    return (
        <div className={`fixed-top ${show === false ? "d-none" : "d-block"}`} >
            <Alert key={condition} variant={condition}>
                {msg}
            </Alert>
        </div>
    );
}

export default MyAlert;