import React, { useState, useContext } from 'react'
import { Form, Button } from 'react-bootstrap';
import BlogContext from '../context/Blogs/BlogContext';
import { useNavigate } from 'react-router-dom';
function Login() {
    let navigate = useNavigate()
    const { login, getUser, setToken, fetchBlogs, setShowAlert, setShowLoader, isChecked, fetchAllBlogs } = useContext(BlogContext)
    const [credentials, setCredentials] = useState({
        email: "",
        password: "",
    });
    const handleSubmit = async (e) => {
        try {
            e.preventDefault();
            await login(credentials);
            setToken(localStorage.getItem("authToken"))
            setShowLoader(true);
            await getUser();
            if (isChecked === true) {
                await fetchBlogs();
            }
            else {
                await fetchAllBlogs()
            }
            setShowLoader(false);

            await setShowAlert({
                show: true,
                msg: "Loginned Succesfully",
                condition: "success"
            })

            setTimeout(() => {
                setShowAlert({
                    show: false
                })


            }, 1000);
            navigate("/Blogs")


        }
        catch (error) {
            setShowAlert({
                show: true,
                msg: error.toString(),
                condition: "danger"
            })
            setTimeout(() => {
                setShowAlert({
                    show: false
                })
            }, 1000);
        }
    }
    const onChange = (e) => {
        setCredentials({ ...credentials, [e.target.name]: e.target.value })

    }
    return (

        <div className="container-fluid bgImgCont d-flex justify-content-center align-items-center vh-100">
            <Form onSubmit={handleSubmit} >
                <h1 className='mb-4 '>Login</h1>

                <Form.Group className="mb-3" controlId="formBasicEmail" >
                    <Form.Label>Email address</Form.Label>
                    <Form.Control
                        size="lg"
                        type="email"
                        placeholder="Enter email..."
                        className="rounded-pill"
                        onChange={onChange}
                        name='email'
                        required
                    />
                    <Form.Text className="text-secondary">
                        We'll never share your email with anyone else.
                    </Form.Text>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword" >
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                        size="lg"
                        type="password"
                        placeholder="Password..."
                        className="rounded-pill"
                        onChange={onChange}
                        name='password'
                        required

                    />
                </Form.Group>



                <Button variant="primary" type="submit" className="rounded-pill w-100 btnCol bgBtnCol">
                    Submit
                </Button>
            </Form>
        </div>
    )
}

export default Login