import React, { useContext, useState } from 'react'
import Card from 'react-bootstrap/Card';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import BlogContext from '../context/Blogs/BlogContext';
import { useNavigate } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import { EditorState, convertFromRaw } from 'draft-js';

const MAX_PREVIEW_LENGTH = 100;

function BlogItem(props) {
    const navigate = useNavigate()
    const { deleteBlog, fetchBlogs, getOldData, setShowAlert, token, isChecked, userId, fetchAllBlogs } = useContext(BlogContext)
    const { title, description, id, user, image } = props;
    // Function to generate a limited preview text
    const generateLimitedPreview = () => {
        const currentContent = editorState.getCurrentContent();
        const plainText = currentContent.getPlainText('');
        const limitedPreview = plainText.slice(0, MAX_PREVIEW_LENGTH);
        return limitedPreview;
    };

    const parsedContent = JSON.parse(description)
    const contentState = convertFromRaw(parsedContent)
    const editorState = EditorState.createWithContent(contentState)
    // eslint-disable-next-line
    const [myEditorState, setMyEditorState] = useState(editorState)

    const navigateToBlogPage = async () => {
        localStorage.setItem("blogId", id)
        navigate("/BlogPage");
    }
    const handleDelete = async (BlogId) => {
        await deleteBlog(BlogId);
        if (isChecked === true) {
            await fetchBlogs()

        }
        else {
            await fetchAllBlogs()
        }
        setShowAlert({
            show: true,
            msg: "Blog has Been Deleted Successfully!",
            condition: "danger"
        })
        setTimeout(() => {
            setShowAlert({ show: false })
        }, 1000);
    }
    const handleEdit = async (title, description2, id, image) => {

        await getOldData(title, description2, id, image);
        navigate("/EditBlog");
    }
    return (


        <Card
            bg={"light"}
            key={"light"}
            text={"dark"}
            style={{}}
            className=" mb-2 transition"
        >
            <Card.Header className='d-flex justify-content-between'>Blog
                {(token !== null && isChecked) || (token !== null && userId === user) ? <div className='icons'>
                    <FontAwesomeIcon icon={faEdit} className='f-icon mx-1' onClick={() => handleEdit(title, myEditorState, id, image)} />
                    <FontAwesomeIcon icon={faTrash} className='f-icon mx-1' onClick={() => {
                        handleDelete(id)
                    }} />

                </div> : undefined}
            </Card.Header>
            <Card.Img variant="top" src={image} />
            <Card.Body>
                <Card.Title>{title}... </Card.Title>
                <Card.Text>
                    {/* <Editor readOnly={true} editorState={myEditorState} />... */} {generateLimitedPreview()}...
                </Card.Text>
                <Button className='bgBtnCol btnCol  p-2 ' onClick={navigateToBlogPage}> View Blog</Button>
            </Card.Body>
        </Card>

    );
}

export default BlogItem