import React, { useContext, useEffect, useState } from 'react'
import BlogContext from '../context/Blogs/BlogContext'
import { Col, Row, Container } from 'react-bootstrap'
import { Editor, EditorState, convertFromRaw } from 'draft-js';


const BlogPage = () => {
    const { blog, fetchBlog } = useContext(BlogContext)
    const [myEditorState, setMyEditorState] = useState(null)
    const [formatedDate, setFormatedDate] = useState(null)
    useEffect(() => {
        const fetchBLogData = async () => {
            await fetchBlog(localStorage.getItem("blogId"))

        }
        fetchBLogData();
    }, [])
    useEffect(() => {
        if (blog) {

            let date = new Date(blog.date)
            setFormatedDate(date.toString());
            const parsedContent = JSON.parse(blog.description)
            const contentState = convertFromRaw(parsedContent)
            const editorState = EditorState.createWithContent(contentState)
            setMyEditorState(editorState)
        }

    }, [blog])



    if (!myEditorState) {
        return <h1>Loading...</h1>
    }

    return (
        <Container >
            <Row>
                <h1 className='my-4 '>{blog.title}</h1>
                <p>Posted On {formatedDate}</p>
                <img src={blog.image} alt='' />
                <Row className='mt-4'>
                    <Col sm={8}>
                        {
                            <Editor editorState={myEditorState} readOnly={true} />
                        }
                    </Col>

                </Row>
                {/* <Col sm={2}>
                    <h2 className='text-center'>Other Blogs</h2>
                    {

                        allFetchedBlogs.map((myBlog) => {
                            return (
                                blog._id !== myBlog._id ?
                                    <a className='text-center my-4'>{myBlog.title}</a> : null)
                        })}
                </Col> */}
            </Row>
        </Container>
    )
}

export default BlogPage