import React, { useState, useContext } from 'react'
import { Form, Button } from 'react-bootstrap';
import BlogContext from '../context/Blogs/BlogContext';
import { useNavigate } from 'react-router-dom'
import RichTextEd from './RichTextEd';

function EditBlog() {
    let navigate = useNavigate()
    const { fetchBlogs, editBlog, oldBlogData, setShowAlert, fetchAllBlogs, editorData } = useContext(BlogContext)
    const [BlogData, setBlogData] = useState({
        title: oldBlogData.title,
        description: oldBlogData.description,
        id: oldBlogData.id,
        image: oldBlogData.image
    })

    const handleSubmit = async (e) => {
        e.preventDefault();
        const newData = {
            title: BlogData.title,
            description: editorData,
            id: BlogData.id,
            image: BlogData.image
        }
        try {
            await editBlog(newData);

        } catch (error) {
            console.log(error.toString())
        }
        // setBlogData({
        //     title: "",
        //     description: ""
        // })
        await fetchBlogs();
        await fetchAllBlogs()
        setShowAlert({
            show: true,
            msg: "Blog has Been Edited Successfully!",
            condition: "success"
        })
        setTimeout(() => {
            navigate("/Blogs")
            setShowAlert({
                show: false
            })

        }, 1000)

    }
    const onChange = (e) => {
        setBlogData({ ...BlogData, [e.target.name]: e.target.value })
    }
    return (
        <div className="container vh-100">
            <Form onSubmit={handleSubmit} >
                <h1 className='mb-4 '>Edit Blog</h1>

                <Form.Group className="mb-3" controlId="formBasicText" >
                    <Form.Label>Title</Form.Label>
                    <Form.Control
                        size="lg"
                        type="text"
                        placeholder="Enter title..."
                        className="rounded-pill"
                        value={BlogData.title}
                        onChange={onChange}
                        name='title'
                        required
                    />

                </Form.Group>
                <Form.Group className='mb-3' controlId='formBasicImg'>
                    <Form.Label>Main/Top Image</Form.Label>
                    <Form.Control type='Link' placeholder='Paste Your Image Link Here...' name='image' value={BlogData.image} onChange={onChange} required />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicText" >
                    <Form.Label>Blog</Form.Label>
                    {<RichTextEd es={BlogData.description} />}
                </Form.Group>



                <Button variant="primary" type="submit" className="rounded-pill w-100">
                    Submit
                </Button>
            </Form>
        </div>
    )
}

export default EditBlog