
//    All of the imports are here...

//  Importing css here...
import './App.css';
import { Routes, Route, useNavigate } from "react-router-dom"
import { useContext, useEffect } from 'react';

//   All of the imported components are here...
import MyNavbar from './components/MyNavbar';
import SignUp from './components/SignUp';
import Login from './components/Login';
import Blogs from './components/Blogs';
import AddBlog from "./components/AddBlog"
import MyAlert from './components/MyAlert';
import Home from "./components/Home"
import BlogContext from './context/Blogs/BlogContext';
import EditBlog from './components/EditBlog';
import BlogPage from './components/BlogPage';
import Loader from './components/Loader';

//   Start...
function App() {
  let navigate = useNavigate()

  //  Importing global functions and states used in this component...
  const { showAlert, token, getUser, fetchBlogs, showLoader, fetchAllBlogs, isChecked } = useContext(BlogContext)


  // Redirect To The index file of the website whenever window is loaded...
  // window.addEventListener("load", () => {
  //   navigate("/")
  // })
  useEffect(() => {
    const verifying = async () => {

      try {
        let authToken = localStorage.getItem("authToken")
        if (authToken !== null && isChecked === true) {
          await getUser();
          await fetchBlogs()
        }
        else if (authToken === null || isChecked === false) {
          await fetchAllBlogs()

        }
      }
      catch (error) {
        console.log(error.toString())
      }
    }
    verifying();
  }, [isChecked])


  return (
    <>
      <MyNavbar />
      <MyAlert show={showAlert.show} msg={showAlert.msg} condition={showAlert.condition} />
      <Loader showLoader={showLoader} />
      <Routes>
        <Route exact path='/' element={<Home />}></Route>
        <Route exact path="/SignUp" element={<SignUp />}></Route>
        <Route exact path="/Login" element={<Login />}></Route>
        <Route exact path="/Blogs" element={(<Blogs />)}></Route>
        <Route exact path="/AddBlog" element={token === null || token === undefined ? (<Login />) : (<AddBlog />)}></Route>
        <Route exact path="/EditBlog" element={token === null || token === undefined ? (<Login />) : (<EditBlog />)}></Route>
        <Route exact path="/BlogPage" element={<BlogPage />}></Route>

      </Routes>
    </>
  );
}

export default App;
