import React from 'react' 
import myImage from "../loader.svg"
function Loader(props) {
  return (
    <div className={`${props.showLoader===false?"d-none":"d-flex"} fixed-top justify-content-center align-items-center w-100 h-100 bg-secondary`}>
        <img src={myImage} />
        </div>
  )
}

export default Loader