//    All of the imports are here...

import React from 'react'
import { Button } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'
function Home() {
  let navigate = useNavigate()
  return (
    <>
      <div className='container-fluid bgImgCont d-flex  justify-content-center flex-column'>
        <h1 className=' '><span className='spText2'>Unlocking</span> the <span className='spText1'>Power</span> of <span className='spText2'>Technology</span></h1>
        <p className='display-1 silver'>Embark on a journey through the ever-evolving landscape of <span className='spText1'>Technology</span>.</p>
        <div>
          <Button className='bgBtnCol btnCol btnSize m-3 p-3 ' onClick={() => {
            navigate("/Blogs")
          }}>
            View Blogs</Button>
        </div>
      </div>
    </>
  )
}

export default Home