import React, { useState, useRef, useContext } from 'react';
import { Editor, EditorState, RichUtils, getDefaultKeyBinding, AtomicBlockUtils, convertToRaw } from 'draft-js';
import BlogContext from '../context/Blogs/BlogContext';

const styleMap = {
    CODE: {
        backgroundColor: 'rgba(0, 0, 0, 0.05)',
        fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
        fontSize: 16,
        padding: 2,
    },
};

const getBlockStyle = (block) => {
    switch (block.getType()) {
        case 'blockquote':
            return 'RichEditor-blockquote';
        default:
            return null;
    }
};

const StyleButton = ({ active, label, onToggle, style }) => {
    const handleToggle = (e) => {
        e.preventDefault();
        onToggle(style);
    };

    let className = 'RichEditor-styleButton';
    if (active) {
        className += ' RichEditor-activeButton';
    }

    return (
        <span className={className} onMouseDown={handleToggle}>
            {label}
        </span>
    );
};

const BLOCK_TYPES = [
    { label: 'H1', style: 'header-one' },
    { label: 'H2', style: 'header-two' },
    { label: 'H3', style: 'header-three' },
    { label: 'H4', style: 'header-four' },
    { label: 'H5', style: 'header-five' },
    { label: 'H6', style: 'header-six' },
    { label: 'Blockquote', style: 'blockquote' },
    { label: 'UL', style: 'unordered-list-item' },
    { label: 'OL', style: 'ordered-list-item' },
    { label: 'Code Block', style: 'code-block' },
];

const BlockStyleControls = ({ editorState, onToggle }) => {
    const selection = editorState.getSelection();
    const blockType = editorState
        .getCurrentContent()
        .getBlockForKey(selection.getStartKey())
        .getType();

    return (
        <div className="RichEditor-controls">
            {BLOCK_TYPES.map((type) => (
                <StyleButton
                    key={type.label}
                    active={type.style === blockType}
                    label={type.label}
                    onToggle={onToggle}
                    style={type.style}
                />
            ))}
        </div>
    );
};

const INLINE_STYLES = [
    { label: 'Bold', style: 'BOLD' },
    { label: 'Italic', style: 'ITALIC' },
    { label: 'Underline', style: 'UNDERLINE' },
    { label: 'Monospace', style: 'CODE' },
];

const InlineStyleControls = ({ editorState, onToggle }) => {
    const currentStyle = editorState.getCurrentInlineStyle();

    return (
        <div className="RichEditor-controls">
            {INLINE_STYLES.map((type) => (
                <StyleButton
                    key={type.label}
                    active={currentStyle.has(type.style)}
                    label={type.label}
                    onToggle={onToggle}
                    style={type.style}
                />
            ))}
        </div>
    );
};

const ImageBlock = ({ contentState, block, blockProps }) => {
    const entity = contentState.getEntity(block.getEntityAt(0));
    const data = entity.getData();
    const src = data.src;
    const { setEditorState, editorState } = blockProps;

    const handleEditImage = () => {
        const imageUrl = window.prompt('Edit image URL:', src);
        if (imageUrl) {
            const contentStateWithEntity = contentState.createEntity(
                'IMAGE',
                'IMMUTABLE',
                { src: imageUrl }
            );
            const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
            const newEditorState = EditorState.set(
                editorState,
                { currentContent: contentStateWithEntity }
            );
            setEditorState(
                AtomicBlockUtils.replaceAtomicBlock(newEditorState, block.getKey(), entityKey)
            );
        }
    };

    return (
        <div style={{ maxWidth: '100%', display: 'block', cursor: 'pointer' }} onClick={handleEditImage}>
            <img src={src} alt="Embedded" style={{ maxWidth: '100%', display: 'block' }} />
        </div>
    );
};
//   START...
const RichTextEd = ({ es }) => {
    const [editorState, setEditorState] = useState(
        es === "empty" ? EditorState.createEmpty() : es
    );
    // eslint-disable-next-line
    const { editorData, setEditorData } = useContext(BlogContext)
    const editorRef = useRef(null);

    const handleKeyCommand = (command) => {
        const newState = RichUtils.handleKeyCommand(editorState, command);
        if (newState) {
            setEditorState(newState);
            return true;
        }
        return false;
    };

    const mapKeyToEditorCommand = (e) => {
        if (e.keyCode === 9 /* TAB */) {
            const newEditorState = RichUtils.onTab(
                e,
                editorState,
                4 /* maxDepth */
            );
            if (newEditorState !== editorState) {
                setEditorState(newEditorState);
            }
            return;
        }
        return getDefaultKeyBinding(e);
    };

    const toggleBlockType = (blockType) => {
        setEditorState(RichUtils.toggleBlockType(editorState, blockType));
    };

    const toggleInlineStyle = (inlineStyle) => {
        setEditorState(RichUtils.toggleInlineStyle(editorState, inlineStyle));
    };
    const rawContentState = convertToRaw(editorState.getCurrentContent());
    const jsonData = JSON.stringify(rawContentState);
    setEditorData(jsonData)
    const contentState = editorState.getCurrentContent();
    const className = !contentState.hasText() && contentState.getBlockMap().first().getType() !== 'unstyled'
        ? 'RichEditor-editor RichEditor-hidePlaceholder'
        : 'RichEditor-editor';

    const focus = () => editorRef.current.focus();
    const onChange = (editorState) => {
        setEditorState(editorState);
        const rawContentState = convertToRaw(editorState.getCurrentContent());
        const jsonData = JSON.stringify(rawContentState);
        setEditorData(jsonData)
    }

    const blockRendererFn = (block) => {
        if (block.getType() === 'atomic') {
            return {
                component: ImageBlock,
                editable: false,
                props: { contentState, setEditorState, editorState }, // Pass the required props
            };
        }
        return null;
    };
    const insertImage = () => {
        const imageUrl = window.prompt('Enter image URL:');
        if (imageUrl) {
            const contentStateWithEntity = editorState.getCurrentContent().createEntity(
                'IMAGE',
                'IMMUTABLE',
                { src: imageUrl }
            );
            const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
            setEditorState(
                AtomicBlockUtils.insertAtomicBlock(editorState, entityKey, ' ')
            );
        }
    };
    return (
        <div className="RichEditor-root">
            <BlockStyleControls editorState={editorState} onToggle={toggleBlockType} />
            <div>
                <button onClick={insertImage}>Insert Image</button>
            </div>
            <InlineStyleControls editorState={editorState} onToggle={toggleInlineStyle} />
            <div className={className} onClick={focus}>
                <Editor
                    blockStyleFn={getBlockStyle}
                    customStyleMap={styleMap}
                    editorState={editorState}
                    handleKeyCommand={handleKeyCommand}
                    keyBindingFn={mapKeyToEditorCommand}
                    onChange={onChange}
                    placeholder="Write Here..."
                    ref={editorRef}
                    spellCheck={true}
                    blockRendererFn={blockRendererFn}
                />
            </div>
        </div>
    );
};

export default RichTextEd;
