import React, { useState } from "react";
import BlogContext from "./BlogContext";

const BlogState = (props) => {

    // All states are here...
    const [showLoader, setShowLoader] = useState(false)
    const [showAlert, setShowAlert] = useState({
        show: false,
        msg: null,
        condition: "success"
    })
    const [editorData, setEditorData] = useState()
    const [blog, setBlog] = useState(null)
    const [token, setToken] = useState(localStorage.getItem("authToken"))
    const host = "https://bloghubbackend.glitch.me";
    const [fetchedBlogs, setFetchedBlogs] = useState([])
    const [allFetchedBlogs, setAllFetchedBlogs] = useState([])
    const [isChecked, setIsChecked] = useState(false)
    const [blogsToDisplay, setBlogsToDisplay] = useState([])
    const [userName, setUserName] = useState()
    const [userId, setUserId] = useState()
    // Making  a request /api/auth/createUser
    const createUser = async (credentials) => {
        try {
            const { name, email, password } = credentials;

            const response = await fetch(`${host}/api/auth/createUser`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({ name, email, password })
            })
            const json = await response.json();
            if (json.success === false) {
                throw new Error(json.error)
            }
            else {

                console.log(json)
            }

        }
        catch (err) {
            throw err
        }
    }

    // Making a request /api/auth/Login.
    const login = async (credentials) => {
        try {
            const { email, password } = credentials
            const response = await fetch(`${host}/api/auth/login`, {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify({ email, password })
            })
            const json = await response.json();
            if (json.success === false) {
                throw new Error(json.error);
            }
            else {
                localStorage.setItem("authToken", json.authToken)
                await fetchAllBlogs()
            }
        }
        catch (error) {
            throw error;
        }
    }

    // Making request /api/auth/getUser
    const getUser = async () => {
        try {
            const response = await fetch(`${host}/api/auth/getUser`, {
                method: "GET",
                headers: {
                    "content-type": "application/json",
                    "authToken": localStorage.getItem("authToken")
                }
            });
            const json = await response.json();
            if (json.success === false) {
                throw new Error(json.error);
            }
            setUserId(json.user._id)
            setUserName(json.user.name)
        }
        catch (error) {
            console.log(error)
        }
    }

    // Making Request /api/blog/fetchBlogs

    const fetchBlogs = async () => {
        try {
            const response = await fetch(`${host}/api/blog/fetchBlogs`, {
                method: "GET",
                headers: {
                    "content-type": "application/json",
                    "authToken": localStorage.getItem("authToken")
                }

            })
            const json = await response.json()

            if (json.success === false) {
                throw new Error("Failed To Fetch Blogs!")
            }
            else {
                setFetchedBlogs(json.blogs)
                await filterBlogs(json.blogs)

            }
        }
        catch (error) {
            throw error;
        }
    }

    //  Making Request /api/blog/fetchAllBlogs

    const fetchAllBlogs = async () => {
        try {
            const response = await fetch(`${host}/api/blog/fetchAllBlogs`, {
                method: "GET",
                headers: {
                    "content-type": "application/json"
                },
            })
            let json = await response.json()
            if (json.success === false) {
                throw new Error("Failed To Fetch Blogs!")
            }


            setAllFetchedBlogs(json.fetchedBlogs)

            await filterBlogs(json.fetchedBlogs)
        } catch (error) {
            throw error;
        }
    }

    // Making request /api/blog/fetchBlog

    const fetchBlog = async (blogId) => {
        try {
            const response = await fetch(`${host}/api/blog/fetchBlog/${blogId}`, {
                method: "GET",
                headers: {
                    "content-type": "application/json"
                }
            })
            const json = await response.json()
            if (json.success === false) {
                throw new Error("Failed To Fetch Blog!")
            }
            setBlog({ title: json.blog.title, description: json.blog.description, date: json.blog.date, _id: json.blog._id, image: json.blog.image })
        }
        catch (error) {
            throw error
        }
    }
    // Making request /api/blog/addBlog
    const addBlog = async (BlogData) => {
        try {
            const { title, description, image } = BlogData
            const response = await fetch(`${host}/api/blog/addBlog`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "authToken": localStorage.getItem("authToken")
                },
                body: JSON.stringify({ title, description, image })


            })
            const json = response.json();
            console.log(json)
            if (json.success === false) {
                throw new Error("Failed To Add Blog...")
            }
            else {

            }
        }
        catch (err) {
            throw err;
        }
    }

    // Making request /api/blog/deleteBlog
    const deleteBlog = async (id) => {
        try {
            const response = await fetch(`${host}/api/blog/deleteBlog/${id}`, {
                method: "DELETE",
                headers: {
                    "content-type": "application/json",
                    "authToken": localStorage.getItem("authToken"),

                }
            })
            const json = await response.json();
            if (json.success === false) {
                throw new Error("Falied To Delete Blog...")
            }
            else {
                await fetchBlogs();
            }
        }
        catch (error) {
            throw error;
        }
    }

    let oldBlogData = {}
    const getOldData = (title, description, id, image) => {
        oldBlogData.title = title;
        console.log(description)
        oldBlogData.description = description;
        oldBlogData.id = id;
        oldBlogData.image = image

    }
    // Making request /api/blog/updateBlog
    const editBlog = async (BlogData) => {
        try {
            const { title, description, id, image } = BlogData
            const response = await fetch(`${host}/api/blog/editBlog/${id}`, {
                method: "PUT",
                headers: {
                    "content-type": "application/json",
                    "authToken": localStorage.getItem("authToken")
                },
                body: JSON.stringify({ title, description, image })

            })
            const json = await response.json()

            if (json.success === false) {
                throw new Error(json.error)
            }
            else {
                console.log(json.msg)
            }
        } catch (error) {
            throw error;
        }
    }


    const filterBlogs = async (myBlogsToShow) => {
        setBlogsToDisplay(myBlogsToShow)
    }

    return (
        <BlogContext.Provider value={{ getUser, createUser, login, token, setToken, userName, fetchBlogs, fetchedBlogs, addBlog, deleteBlog, getOldData, oldBlogData, editBlog, showAlert, setShowAlert, showLoader, setShowLoader, fetchAllBlogs, allFetchedBlogs, isChecked, setIsChecked, blogsToDisplay, setBlogsToDisplay, userId, fetchBlog, blog, editorData, setEditorData, filterBlogs }}>{props.children}</BlogContext.Provider>
    )
}
export default BlogState;